import { DialogService } from '@aigens/core/src/app/framework';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-root',
    templateUrl: './root.component.html',
    styleUrls: ['./root.component.scss']
})
export class RootComponent implements OnInit {

    constructor(private dialogService: DialogService, private matDialog: MatDialog, private snack: MatSnackBar, private route: ActivatedRoute) { 

        console.log("Test Root Component constructor");

        this.dialogService.dismissPreLoader();

        this.dialogService.setDialogController(matDialog);
        this.dialogService.setSnackBar(snack);

    }


    

    ngOnInit(): void {
    }

    ngAfterViewInit() {

    }


}
