import { Injectable } from '@angular/core';
import { windows } from 'bowser';

@Injectable({
    providedIn: 'root'
})
export class BridgeService {

    lastRequest: any;
    lastResponse: any;

    // callbacks = {};
    // listening = false;
    my : any;
    constructor() {

        this.my = BridgeService.getMy();
    }

    private static getMy() {

        var my = window['my'];
        if (!my) return null;

        if (!window['aigensMiniappBridgeListening']) {
            window['aigensMiniappBridgeListening'] = true;
            this.listenMessage(my);
        }

        return my;
    }

    isAvailable(): boolean {
        var my = BridgeService.getMy();
        if (my && my.postMessage) return true;
        return false;
    }

    private static listenMessage(my) {
        const oldCallback = my.onMessage;

        console.log("listeningMessage Bridge service", oldCallback);


        my.onMessage = (res) => {

            console.log("received mini message", res);
            // this.lastResponse = res;

            //this.cd.detectChanges();

            var cid = res.callbackId;
            if (cid) {

                /*
                var callback = this.callbacks[cid];
                delete this.callbacks[cid];
                if (callback) {
                    callback(res.response);
                }
                */
                this.doCallbackStatic(cid, res.response);
            }
            else if (res?.serialId) {
                console.log("ali bridge format callback")
                oldCallback && oldCallback(res);

            }

        };

    }


    private doCallback(callbackId, response) {

        let callback = window?.['aigensMiniCallbacks']?.[callbackId];
        delete window?.['aigensMiniCallbacks']?.[callbackId];
        if (callback) {
            callback(response);
        }
    }

    private static doCallbackStatic(callbackId, response) {

        let callback = window?.['aigensMiniCallbacks']?.[callbackId];
        delete window?.['aigensMiniCallbacks']?.[callbackId];
        if (callback) {
            callback(response);
        }
    }


    //invoke Alipay framework method
    invokeMethod(method: string, params: any[], callback: Function, timeout: number = 0) {

        var callbackId = method + "_" + BridgeService.getRandomString(7);

        var message = {
            callbackId: callbackId,
            api: "alipay",
            method: method,
            params: params,
            url: window.location.href,
            response: null
        };

        this.lastRequest = message;

        console.log("sending mini app request message", message);

        var my = BridgeService.getMy();
        if (!(my && my.postMessage)) {
            console.log("MY object not found");
            this.doCallback(callbackId, null);
            return;
        }

        try {
            if (!window['aigensMiniCallbacks']) { window['aigensMiniCallbacks'] = {}; }
            window['aigensMiniCallbacks'][callbackId] = callback;

            if (timeout > 2000) {
                console.log("setting timeout", timeout);
                setTimeout(() => {
                    console.log("timeout now", timeout);
                    this.doCallback(callbackId, null);
                }, timeout);
            }

            if (my) my.postMessage(message);

        } catch (e) {
            alert("error:" + e);
        }

        //this.my2.postMessage(message);

        //this.my2.postMessage("HELLO");

    }

    public static invokeMethodStatic(method: string, params: any[], callback: Function) {


        var callbackId = method + "_" + BridgeService.getRandomString(7);

        var message = {
            callbackId: callbackId,
            api: "alipay",
            method: method,
            params: params,
            url: window.location.href,
            response: null
        };

        // this.lastRequest = message;

        console.log("sending mini app request message", message);

        var my = BridgeService.getMy();
        if (!(my && my.postMessage)) {
            console.log("MY object not found");
            this.doCallbackStatic(callbackId, null);
            return;
        }

        try {
            if (!window['aigensMiniCallbacks']) { window['aigensMiniCallbacks'] = {}; }
            window['aigensMiniCallbacks'][callbackId] = callback;

            // if (timeout > 2000) {
            //     console.log("setting timeout", timeout);
            //     setTimeout(() => {
            //         console.log("timeout now", timeout);
            //         this.doCallbackStatic(callbackId, null);
            //     }, timeout);
            // }

            if (my) my.postMessage(message);

        } catch (e) {
            alert("error:" + e);
        }
    }


    static getRandomString(length, charSet = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789") {

        let randomString = "";
        for (let i = 0; i < length; i++) {
            let randomPoz = Math.floor(Math.random() * charSet.length);
            randomString += charSet.substring(randomPoz, randomPoz + 1);
        }
        // this.randomString = randomString;
        return randomString;
    }

}
