import { Order } from 'aigens-ng-core';
import { FavoriteItemService } from './favorite-item.service';
import { StorageService } from '@aigens/core/src/app/framework/services/storage.service';
import { SubscribeService } from '@aigens/core/src/app/pure/services/subscribe.service';
import { Injectable, Injector } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Category, Data, Image, Item, ItemGroup, Store } from 'aigens-data';
import {
    ConfigService,
    Context,
    DataService,
    MemberContext,
    MemberContextManager,
    MenuManager,
    OrderContext,
    OrderManager,
    PriceManager,
    RecommendationManager
} from 'aigens-framework';
import { forkJoin, Observable, of } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { SettingManager } from '../../../../../core/src/app/pure/managers/setting-manager';
import { PlatformService } from '../../../../../core/src/app/pure/services/platform.service';
import { CateringService } from './catering.service';
import { MenuService } from './menu.service';
import { DirectoryContext } from '@aigens/core/src/app/framework/services/directory-context.service';
import { PreOrderContext } from '@aigens/core/src/app/framework/services/pre-order-context.service';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
    providedIn: 'root'
})
export class OrderContextManager {

    private cachedMenus = {};
    private cachedSlimMenus = {};

    constructor(private injector: Injector, private route: ActivatedRoute, private orderContext: OrderContext, private dataService: DataService, private configService: ConfigService, private storageService: StorageService,
        private recommendationManager: RecommendationManager, private context: Context, private memberContextManager: MemberContextManager, private settingManager: SettingManager,
        private memberContext: MemberContext,
        public menuService: MenuService, private subscribe: SubscribeService, private cateringService: CateringService, private platformService: PlatformService, private priceManager: PriceManager,
        private orderManager: OrderManager, private directoryContext: DirectoryContext, private favoriteItemService: FavoriteItemService,
        private preOrderContext: PreOrderContext, public t: TranslateService) {

        this.subscribe.getObservable('order').subscribe((order) => {

            console.log('order changed', order);
            this.orderContext.save();
        });


    }


    getMenuWithSession(storeId: any, mode: string, lang: string = 'multi', refresh: boolean = false, byPassCache: boolean = false): Observable<any> {

        console.log("getMenuWithSession2");
        var menu$ = this.getMenuRaw(storeId, mode, lang, refresh);
        var session$ = this.getOrderSession(storeId, mode, lang, false, byPassCache);

        return forkJoin([menu$, session$]).pipe(tap(e => this.postMenuProcesses(false)));
    }

    getMenu(storeId: any, mode: string, lang: string = 'multi', refresh = false, moduleType: 'merchant' | 'byod' = 'byod'): Observable<any> {

        return this.getMenuRaw.apply(this, arguments).pipe(tap(e => this.postMenuProcesses()));
    }

    private getMenuRaw(storeId: any, mode: string, lang: string = 'multi', refresh = false, moduleType: 'merchant' | 'byod' = 'byod'): Observable<any> {

        console.log("getMenu");
        mode = this.menuService.menuManager?.debugMode ?? mode;
        if (!moduleType) moduleType = 'byod';
        let baseUrl = '/api/v1/menu/store/';
        if (moduleType === 'merchant') baseUrl = '/api/v1/merchant/store/';
        var url = baseUrl + storeId + '.json?menu=' + mode + '&locale=' + lang; // : brand had set cod-cash-payemnt with modes'[takeaway']; it will return cod-cash payemnt with keyword(menu=takeaway),and if use mode=takeaway, it will filter cod-cash-payment

        var channel = this.platformService.getMenuChannel();
        if (channel) {
            url += "&channel=" + channel;
        }

        var container = this.platformService.getContainer();
        if (container) {
            url += "&container=" + container;
        }

        url += "&ngsw-bypass=true";

        var store = this.getCachedMenuStore(url);

        if (store && !refresh) {

            console.log('MENU CACHED');

            this.setMenuData(url, store, true);

            return of(store);
        }

        console.log("getMenu fetch");

        // return this.dataService.get(url, {}, false, true).pipe(map(jo => this.setMenuData(url, Data.toData(Store, jo))));
        return this.dataService.get(url, {}, false, true).pipe(map(jo => {
            let retStore = Data.toData(Store, jo);

            if(!retStore.menu){
                throw new Error(this.t.instant('pages.tableList.noMenuError'));
            }else{
                return this.setMenuData(url, retStore);
            }

        }));

    }

    private postMenuProcesses(needRecalculateAvailableCat = true) {
        console.log('postMenuProcess');
        // once session is set, check spot period price rule, so move to here
        this.applyPriceRule();
        //once session is set, call this to check session.passes and recal cat will show the passes
        this.setPassMarkers();

        if (needRecalculateAvailableCat) {
            this.recalculateAavailableCategories();
        }


    }

    /**
     * This is a method to fetch menu from API and do some map on it.
     * But we don't change any context.
     * Purpose:
     * - get menu from other store and validate the menu against user cart
     *
     * @param storeId
     * @param mode
     * @param lang
     */
    getMenuWithoutChangeAnyContext(storeId: any, mode: string, lang: string = 'multi'): Observable<any> {

        var url = '/api/v1/menu/store/' + storeId + '.json?menu=' + mode + '&locale=' + lang;

        var container = this.platformService.getContainer();
        if (container) {
            url += "&container=" + container;
        }

        var channel = this.platformService.getMenuChannel();
        if (channel) {
            url += "&channel=" + channel;
        }

        var store = this.getCachedMenuStore(url);

        if (store) {
            return of(store);
        }

        return this.dataService.get(url).pipe(map(jo => {
            store = Data.toData(Store, jo);
            const mm = new MenuManager(this.injector);
            mm.processMenu(store.menu);

            // closed first, because sometimes give unstable behavior like change mode not changing directly
            // this.postMenuProcesses();

            return store;
        }));
    }


    /**
     * Fetch & store slim menu. Now, we use it for handling unlock category in court mode.
     *
     * @param storeIds
     * @param mode
     * @param lang
     */
    fetchAndStoreSlimMenu(storeIds: any[], mode: string, lang: string = 'multi'): void {
        this.orderContext.slimMenu = {};

        storeIds.forEach(id => {
            let url = '/api/v1/menu/store/' + id + '.json?menu=' + mode + '&locale=' + lang;

            const container = this.platformService.getContainer();
            if (container) {
                url += '&container=' + container;
            }

            const channel = this.platformService.getMenuChannel();
            if (channel) {
                url += '&channel=' + channel;
            }

            let store = this.getCachedSlimMenuStore(url);

            if (store) {
                this.orderContext.slimMenu[id] = store;
            }

            this.dataService.get(url).pipe(map(jo => {
                store = Data.toData(Store, jo);
                const mm = new MenuManager(this.injector);
                mm.processMenu(store.menu);

                this.orderContext.slimMenu[id] = store;
            })).subscribe(x => {
            });
        });
    }

    private setMenuSetting(store) {

        this.settingManager.setStoreMenu(store);
        this.orderContext.next('settingManagerDataChange', true);
        this.subscribe.mergeTranslation(this.t); // update the translation after calling the store.json
        this.settingManager.setForm(this.orderContext?.session?.form);
    }

    getOrderSession(storeId: any, mode: string, lang: string = 'multi', refresh: boolean = false, byPassCache: boolean = false) {

        console.log('getOrderSession starts');

        //check if session created already
        var existing = this.orderContext.session;

        if (existing && !byPassCache) {

            if (existing?.form) {
                this.settingManager.setForm(existing.form);
            }

            if (existing.storeId == storeId && existing.mode == mode) {

                var now = Date.now();


                if (now > existing.expire) {

                    console.log("session expired", new Date(existing.expire));

                } else {

                    console.log("session not expired", new Date(existing.expire));

                    return of(existing);
                }


            }

        }

        var url = '/api/v1/menu/session.json';

        var params = {
            storeId: storeId,
            mode: mode,
            "ngsw-bypass": "true",
            locale: 'multi',
            v: 4,
        };

        if (this.orderContext.spot) {
            params['spot'] = this.orderContext.spot;
        }

        //var sid = this.configService.getSessionId();

        if (mode != 'byod') {
            params['deviceId'] = this.configService.getDeviceId();
            params['guestLogin'] = true;
        }

        if (mode == 'byod') {
            params['session'] = this.orderContext.key || this.route.snapshot.queryParams.key || this.configService.getDeviceId();
            params['spot'] = this.route.snapshot.queryParams.spot;
        }

        var fields = ['membership', 'behavior', 'memberFlow'];

        if ('delivery' == mode || 'takeaway' == mode) {

            fields.push(...['addresses']);

        } else if ('dinein' == mode) {

            fields.push(...['passes']);

        } else if ('byod' == mode) {

            fields.push(...['passes']);
            fields.push(...['order']);
        }

        params['fields'] = fields.join(',');
        params['needForm'] = true;


        console.log("getOrderSession fetch");

        return this.dataService.postWithParams(url, params).pipe(map(jo => this.setOrderSessionData(storeId, mode, jo)));

    }

    private setOrderSessionData(storeId, mode, data) {

        console.log('setOrderSessionData', data);

        this.orderContext.session = data;

        if (data?.form) {
            this.settingManager.setForm(data.form);
        }

        data.storeId = storeId;
        data.mode = mode;

        if (data.behavior) {
            this.orderContext.next('behavior', data.behavior);
        }
        var member = data.member;
        var sessionId = data.sessionId || this.configService.getSessionId();
        if (data.spot) {
            this.orderContext.spotData = data.spot;
        }
        if (sessionId && member) {

            member.sessionId = sessionId;
            if (member && data.membership) {
                member.membership = data.membership;
            }
            this.memberContextManager.setMemberData(member);

        }


        return data;
    }

    public initByodPreView(mode, time): void {
        this.priceManager.setDebugTime(time);
        this.configService.setDebugTime(time);
        this.menuService.menuManager?.setDebugTime(time);
        this.menuService.menuManager.debugMode = mode;
    }
    private setPassMarkers() {

        console.log("enable Pass Cats", this.orderContext.session);

        var resultPasses = this.orderContext.session?.passes;

        if (!resultPasses || !(resultPasses instanceof Array)) {
            return;
        }

        let marker = [];

        let passes: any[] = resultPasses;
        passes.forEach(pass => {
            if (pass && pass['item'] && pass['item']['marker']) {
                marker.push(pass['item']['marker']);
            }
        });

        console.log('marker', marker);
        this.menuService?.setMarkers(marker);

    }

    getCachedMenuStore(url: string) {

        var cached = this.cachedMenus[url];

        if (cached) {
            return cached;
        }

        return null;

    }

    getCachedSlimMenuStore(url: string) {

        var cached = this.cachedSlimMenus[url];

        if (cached) {
            return cached;
        }

        return null;

    }

    //ANY code that changes categories must be handle by this method
    recalculateAavailableCategories(): void {

        console.log('recalculateAavailableCategories', 'SHOULD NOT SEE THIS MANY TIMES');

        var store = this.orderContext.store;
        var cats: Category[] = this.menuService?.processCats(store, this.orderContext?.mode);

        cats = this.personalizeMenu(cats);

        this.orderContext.availableCategories = cats;
        this.orderContext.next("availableCategories", cats);
        this.orderContext?.initMenuCatGroup(); // it should init catGroup function after checking all cat isAvailable
    }

    setMenuData(url, store, fromCache = false) {

        console.log('setMenuData');

        this.orderContext.setMenu(store, fromCache);

        this.setMenuSetting(store);

        console.log('caching menu', url);

        this.cachedMenus[url] = store;

        return store;
    }

    private personalizeMenu(activeCats: Category[]) {

        var personal = this.orderContext.personalMenu;
        console.log('PERSONALIZE MENU', personal);

        if (!personal && this.settingManager.get('menu.sorting', null) !== 'ai') return activeCats;

        //sort the cats
        var result = this.recommendationManager.sortCategories(activeCats, this.memberContext.member, this.orderContext.behavior?.food);

        if (result?.cats) {
            //asign the cats
            activeCats = result.cats;
        }


        for (let cat of activeCats) {

            var mainIg = cat.groups[0];
            var items = mainIg.parentItems;


            var sortedItems = this.recommendationManager.sortItems(items, this.memberContext.member, this.orderContext.behavior?.food);

            mainIg.parentItems = sortedItems;
        }

        return activeCats;

    }


    fakeCount: number = 0;

    makeFakeItems() {

        var items = [];
        for (let i = 0; i < 10; i++) {
            var item = new Item();
            item.name = 'ITEM ' + this.fakeCount;
            item.price = 100.0;
            items.push(item);

            var imgUrl = 'https://via.placeholder.com/150/AA0000?text=' + item.name;
            var image = new Image();
            image.url = imgUrl;
            item.images = { 'default': image } as any;


            this.fakeCount++;
        }

        return items;
    }

    addFakeItems() {

        var menu = this.orderContext.menu;


        var cats = [];

        for (let i = 0; i < 50; i++) {
            var cat = new Category();
            cat.name = 'CAT ' + i;
            var ig = new ItemGroup();
            ig.items = this.makeFakeItems();
            ig.parentItems = ig.items;
            cat.groups = [ig];
            cats.push(cat);

        }

        var concat = menu.categories.concat(cats);

        menu.categories = concat;

        this.orderContext.availableCategories = concat;

    }

    private getOrderMode(): string {
        if (this.orderContext.mode === 'catering') {
            return this.cateringService.getDistributeMethod();
        }
        return this.orderContext.mode;
    }

    getMinOrderAmount() {
        const { menu } = this.orderContext;
        const mode = this.getOrderMode()

        switch (mode) {
            case 'takeaway':
                return menu.settings.minTakeawayTotal;
            case 'delivery':
                return menu.settings.minDeliveryTotal;
            case 'dinein':
                return menu.settings.minDineinTotal;
            default:
                return null;
        }
    }

    getMaxOrderAmount() {
        const { menu } = this.orderContext;
        const mode = this.getOrderMode()

        switch (mode) {
            case 'takeaway':
                return menu.settings.maxTakeawayTotal;
            case 'delivery':
                return menu.settings.maxDeliveryTotal;
            case 'dinein':
                return menu.settings.maxDineinTotal;
            default:
                return null;
        }
    }

    setAllConfirmed(): void {
        this.orderContext.confirmedMode = true;
        this.orderContext.confirmedStore = true;
    }

    /**
     * Re-apply price rule
     */
    refreshPriceRule(forceRecalculate = false): boolean {
        let isRecalMenu = false;

        if (this.orderContext.store?.menu) {
            const priceLevelChange = this.applyPriceRule();
            if (priceLevelChange || forceRecalculate) { // only refresh layout when price level is change
                console.log('price level change');
                // update the menu price
                this.recalculateAavailableCategories();
                isRecalMenu = true;
                // update the cart price
                if (this.orderManager?.order) {
                    const rawOrder = JSON.parse(JSON.stringify(this.orderManager.order));
                    this.orderManager.clearOrderItems();
                    this.orderManager.reconstructOrder(rawOrder);
                }
            }
        }

        return isRecalMenu;
    }

    private applyPriceRule(): boolean {
        if (this.orderContext.store?.menu) {
            this.priceManager.directoryUnixTime = this.directoryContext.getUnixTime();
            this.priceManager.updateBasePrice(this.orderContext.store.menu, this.orderContext.mode,
                this.memberContext.member, this.orderContext.store, this.orderContext.spotData);
            this.priceManager.memberTierPrice(this.orderContext.store.menu, this.orderContext.mode,
                this.memberContext.member, this.orderContext.store);
        }

        return this.priceManager.priceLevelChange;
    }

    // check the maximum order amount allowed
    isMaxOrderAmountAllowedBreached(): { valid: boolean, maxAmountAllowed: any } {
        const maxOrderAmountAllowed = this.getMaxOrderAmount();
        if (maxOrderAmountAllowed) {
            if (maxOrderAmountAllowed < this.orderManager.order?.total ?? 0) {
                return { valid: false, maxAmountAllowed: maxOrderAmountAllowed };
            }
        }

        return { valid: true, maxAmountAllowed: maxOrderAmountAllowed };
    }

    savePreOrder() {

        if (this.settingManager.get('preOrderSameAsPrekiosk', false)) {

            this.preOrderContext.data = this.orderContext.makeSaveData();
            this.postPreOrderSameAsPreKioskOrder();
            this.preOrderContext.save();
            return;
        }
        this.preOrderContext.data = this.orderContext.makeSaveData();
        this.preOrderContext.save();
    }

    hasPreorder(storeId: any, mode: any) {
        if (mode == 'byod' && this.preOrderContext.order) {
            if (this.preOrderContext.storeId == storeId) {
                return true;
            }
        }

        return false;
    }

    disableCheckout() {
        if (this.settingManager.get('allowEmptyOrderToCart', false)) {
            return false;
        }

        if (this.orderManager.hasOfferAlone()) {
            return false;
        }

        // only have minimum order checking now
        if (this.getMinOrderAmount() == null) {
            // minimum order not set
            return this.orderManager.isEmpty() || (!this.isPaylater() && this.orderManager?.order?.total <= 0 && !this.orderManager.isAllowCheckoutZeroTotal(this.orderManager?.order?.total));
        } else {
            if ((this.orderManager.order?.total ?? 0) > 0) {
                return false;
            } else {
                if (this.orderManager.isAllowCheckoutZeroTotal(this.orderManager?.order?.total)) {
                    return false;
                }
                return true;
            }
        }
    }

    isPaylater() {
        return this.orderManager.mode === 'byod' || this.orderManager.mode === 'prekiosk' || this.orderManager.mode === 'preorder';
    }

    async postPreOrderSameAsPreKioskOrder(): Promise<void> {
        const url = '/api/v1/menu/order.json';

        const body = {...this.preOrderContext.data.order};
        body['type'] = 'preorder';
        body['session'] = this.orderManager?.order?.session || this.configService.getSessionId()
        body['takeout'] = 'takeaway' === this.orderManager.mode;

        // if (this.sdkConfigService.isCrystalJade && this.sdkConfigService.getMemberCardNo) {
        //     body['card'] = this.sdkConfigService.getMemberCardNo;
        // }

        await this.storageService.getStorage(this.preOrderContext.contextId).then((x) => {
            if(x?.uuid){
                body['uuid'] = x.uuid
            }
        })

        let returnedOrder = null;
        this.dataService.postWithBody(url, body).subscribe({
            next: (data) => {
                returnedOrder = data?.data;
                this.preOrderContext.order = returnedOrder;
                this.preOrderContext.data = {uuid: returnedOrder.uuid};
                this.preOrderContext.save();
            },
            error: (error) => {
                console.log(error);
            }
        })
    }
}

