
import { Core } from '@aigens/aigens-sdk-core';
import { Injectable, Injector } from '@angular/core';
import { Browser } from '@capacitor/browser';
import { Capacitor } from '@capacitor/core';
import { PlatformService } from '../../../../../core/src/app/pure/services/platform.service';

@Injectable({
    providedIn: 'root'
})
export class NativeContainerService {


    appInfo: any;

    constructor(injector: Injector, private platformService: PlatformService) {

        //super(injector);

        console.log("NativeContainerService constructor");

    }

    init() {
        // init the native method first, Please Keep.
        if (!Capacitor.isNativePlatform()) return;
        try {
            const key = Capacitor.getPlatform() === 'ios' ? 'hsbcpaymepay://' : 'hk.com.hsbc.paymefromhsbc';
            Core.isInstalledApp({
                key
            }).then(r => {
                console.log("hsbcpaymepay install:", r.install);
            })
        } catch (error) {
            console.log("isInstalledApp error:", error);
        }

    }

    echo(msg: any): Promise<any>{

        return Core.echo(msg);

    }

    getMember(): Promise<any>{

        return Core.getMember({});

    }
    getDeeplink(): Promise<any>{

        return Core.getDeeplink({});

    }

    openBrowser(options:any): Promise<any>{

        var url = options.url;
        if(!url) return null;

        console.log("is native??", this.platformService.isNative());

        if(!this.platformService.isNative()){
            console.log("open browswer", url);
            return Browser.open({ url: url});
        }else{
            console.log("open with sdk", url);
            return Core.openBrowser(options);
        }
    }




}
