import { Injectable, Injector } from '@angular/core';
import { Browser } from '@capacitor/browser';
import { SplashScreen } from '@capacitor/splash-screen';
import { RoutingService } from '../../../../../core/src/app/pure/services/routing.service';
import { Capacitor } from '@capacitor/core';
import { App } from '@capacitor/app';
import { Core } from '@aigens/aigens-sdk-core';
import { PlatformService } from '../../../../../core/src/app/pure/services/platform.service';
import { Device } from '@capacitor/device';
@Injectable({
    providedIn: 'root'
})
export class NativeRoutingService extends RoutingService{

    deviceId;
    constructor(injector: Injector) {

        console.log("NativeRoutingService constructor");

        super(injector);


        // Return an unique identifier for the device.
        Device.getId().then(r => {
            const d = r as any;
            this.deviceId = d?.identifier || d?.uuid;
        })
    }

    /*
    openBrowser(options:any){

        var url = options.url;
        if(!url) return;

        Browser.open({ url: url});

    }*/

    openBrowser(options:any): Promise<any>{

        var url = options.url;
        if(!url) return null;

        if(!this.platformService.isNative()){
            console.log("open browswer", url);
            return Browser.open({ url: url});
        }else{
            let member = options?.member || {};
            if (!member["deviceId"]) {
                member["deviceId"] = this.deviceId || "";
                options.member = member;
            }
            console.log("open with sdk", url);
            return Core.openBrowser(options);
        }
    }

    dismissPreLoader() {

        super.dismissPreLoader();

        SplashScreen.hide();
    }


    back() {

        super.back();
    }

    exit(){

        App.exitApp();
    }

    /**
     *
     * @param closedData : Object
     */
     finish(closedData?: any): void {

        Core.finish({ closedData });
    }

    /**
     *
     * @param closedData : Object
     */
    dismiss(closedData?: any): void {

        Core.dismiss({ closedData });
    }


}
