import { Injectable, Injector } from '@angular/core';
import { Category, Data, Image, Store } from 'aigens-data';
import { Context, InventoryManager, MenuManager, OrderContext, OrderManager } from 'aigens-framework';
import * as _ from 'lodash-es';
import { DirectoryContext } from '@aigens/core/src/app/framework/services/directory-context.service';

@Injectable({
    providedIn: 'root'
})
export abstract class BaseMenuService {

    userTypes = [];

    private markers = [];


    public menuManager: MenuManager;
    public context: Context;
    public orderManager: OrderManager;
    public inventoryManager: InventoryManager;
    public orderContext: OrderContext;
    public directoryContext: DirectoryContext;

    constructor(injector: Injector) {

        this.menuManager = injector.get(MenuManager);
        this.context = injector.get(Context);
        this.orderManager = injector.get(OrderManager);
        this.inventoryManager = injector.get(InventoryManager);
        this.orderContext = injector.get(OrderContext);

    }


    /**
     * @description:
     * @param {*} timeZone
     * @param {number} time_num
     * @return {*}
     */
    convertNowDateToStoreTimeZone(timeZone, time_num?: number): Date {
        timeZone = timeZone ? timeZone : 'GMT+8';
        let timeZoneValue = timeZone.split('GMT')[1];
        let integerTimeZoneValue = parseInt(timeZoneValue) * 60;
        let now = this.menuManager.debugTime ? new Date(this.menuManager.debugTime) : new Date();
        if (time_num) {
            now = new Date(time_num);
        }
        let timeZoneOffset = now.getTimezoneOffset();
        let newTime = now.getTime() + (timeZoneOffset + integerTimeZoneValue) * 60000;

        now.setTime(newTime);
        return now;
    }

    /**
     * @description: get dineing time, delivery time or pickup time (in order to get available Categories)
     * @param {*}
     * @return {*}
     */
    abstract getUserSelectTime_number();

    processCats(store: Store, mode: string, refTime = null): Category[] {
        let availableCats = [];
        const time_num = refTime ?? this.menuManager.debugTime ?? this.getUserSelectTime_number();

        let now = time_num;
        if (this.directoryContext?.state?.asap) {
            now = this.convertNowDateToStoreTimeZone(store.timezone, time_num ? time_num : null).getTime();
        }
        console.warn('processCats by time', now, new Date(now));

        this.initUsersTypes(); // init usersTypes first
        console.log('usersTypes', this.userTypes);

        //  getAvailableCategories store
        // let tempStore = Data.toData(Store, JSON.parse(JSON.stringify(store)));
        let tempStore = _.cloneDeep(store);
        availableCats = this.menuManager.getAvailableCategories(tempStore, now, mode === 'takeaway', mode);
        if (!availableCats) {
            return [];
        }
        // console.log('Available cats', availableCats);


        if (this.userTypes?.length > 0) {
            let canShowCats = this.categoriesFliterByUserTypes(availableCats, this.userTypes);
            return canShowCats;
        } else {
            return availableCats;
        }

        // console.log('canShowCats filter by usersType', canShowCats);

        // this.activeCats$.next(canShowCats);
        // return canShowCats;

    }

    setMarkers(markers: Array<any>) {
        if (markers?.length > 0) {
            this.markers = markers;
        }
    }

    initUsersTypes() {
        try {
            this.userTypes = [];
            if (this.context?.member && this.context.member?.method !== 'device') { // 
                !(this.userTypes?.includes('member')) && this.userTypes.push('member');
            }
            if (this.markers?.length > 0) {
                for (let marker of this.markers) {
                    !(this.userTypes?.includes(marker)) && this.userTypes.push(marker);
                }
            }
            this.menuManager.userTypes = this.userTypes;
        } catch (error) {

        }

    }


    private isAllowed(cat, allowed: string[], have: string[]){

        if(!allowed || allowed.length == 0) return true;

        for(let type of allowed){
            if(have.indexOf(type) >= 0){
                console.log("cat allowed", cat.name, allowed, have);
                return true;
            }
        }

        console.log("cat not allowed", cat.name, allowed, have);

        return false;
    }

    private categoriesFliterByUserTypes(cats: Category[], types: string[] = []): Category[] {

        var result = [];

        for(let cat of cats){

            if(this.isAllowed(cat, cat.users, types)){
                result.push(cat);
            }

        }


        return result;
    }


    /********** copy from v3 start **********/
    getDefaultImage(isCategory: boolean, brand: Data, height: number) {
        if (!brand) {
            return null;
        }
        const theme = brand['themes']['byod'];

        if (theme) {

            if (isCategory && theme['category-fb'] && theme['category-fb'].length > 0) {
                const catImage = theme['category-fb'][0] as Image;
                return this.getImageUrl(catImage, height);

            }

            if (!isCategory && theme['item-fb'] && theme['item-fb'].length > 0) {
                const itemImage = theme['item-fb'][0] as Image;
                return this.getImageUrl(itemImage, height);

            }

        }
        return null;

    }

    private getImageUrl(image: Image, width: number) {
        const url = image.url;
        if (!url) {
            return null;
        }
        return Data.getImageUrl(image.url, width, false);
    }

    /********** copy from v3 end **********/

}

