import { Component, Injector, OnInit } from '@angular/core';
import { BasePage } from '../../../../../../core/src/app/framework';

@Component({
    selector: 'app-layout-page',
    templateUrl: './layout-page.component.html',
    styleUrls: ['./layout-page.component.scss']
})
export class LayoutPageComponent extends BasePage implements OnInit {

    numbers = Array(250).fill(0).map((x, i) => i);
    smalls = Array(100).fill(0).map((x, i) => i);

    empty: boolean = false;
    loading: boolean = false;
    over: boolean = false;

    layout: string = "default";
    sub: string = "order-default";

    mainOptions = ["default", "sticky"];
    subOptions = ["order-default", "order-sidecat", "order-kiosk"];

    constructor(public injector: Injector) {


        super(injector);

        console.log("LAYOUT PAGE CONSTRUCTOR");


        var l = this.navParams.get("layout");
        if (l) {
            this.layout = l;
        }

    }

    ngOnInit(): void {
    }

}
