import { Component, NgZone, OnInit } from '@angular/core';
// import { AnimationItem } from 'lottie-web';
// import { AnimationOptions } from 'ngx-lottie';

@Component({
    selector: 'app-animation-tabbar-test-page',
    templateUrl: './animation-tabbar-test-page.component.html',
    styleUrls: ['./animation-tabbar-test-page.component.scss']
})
export class AnimationTabbarTestPageComponent implements OnInit {

    constructor(private ngZone: NgZone) { }

    animationMap = {};

    homeLottieOption: any = {
        path: 'assets/lottie/home.json',
        loop: false
    }

    memberLottieOption: any = {
        path: 'assets/lottie/member.json',
        loop: false
    }

    orderingLottieOption: any = {
        path: 'assets/lottie/ordering.json',
        loop: false
    }

    rewardLottieOption: any = {
        path: 'assets/lottie/reward.json',
        loop: false
    }

    selectedTab: string = 'home';

    ngOnInit(): void {
    }

    animationCreated(animationItem: any, key: string): void {
        this.animationMap[key] = animationItem;
    }

    tabClicked(key): void {
        this.selectedTab = key;
        this.ngZone.runOutsideAngular(() => {
            this.animationMap[key].goToAndPlay(0);
        });
    }

    hasTabSelected(key): boolean {
        return key === this.selectedTab;
    }

}
