import { Component, Injector, OnInit } from '@angular/core';
import { BasePage, MemberContext } from 'aigens-framework';
import { instantMeiliSearch } from '@meilisearch/instant-meilisearch'


declare var instantsearch;

@Component({
    selector: 'app-search-page',
    templateUrl: './search-page.component.html',
    styleUrls: ['./search-page.component.scss']
})
export class SearchPageComponent extends BasePage implements OnInit {


    config: any;

    searchClient = instantMeiliSearch(
        'https://searchtest.aigens.com/', // Host
        '6764009646a9b7b9498369a19f208f801496d99241dce3908f91dabdcc98d397', // API key,
        {
            finitePagination: true
        }
    );

    indexName = "brand";
    //indexName: string = "steam-video-games";

    //ref: https://github.com/meilisearch/instant-meilisearch

    constructor(private injector: Injector) {
        super(injector);



    }


    ngOnInit(): void {


        /*
        var searchClient = instantMeiliSearch(
            "https://ms-adf78ae33284-106.lon.meilisearch.io/",
            "a63da4928426f12639e19d62886f621130f3fa9ff3c7534c5d179f0f51c4f303",
            {
                finitePagination: true
            }
        );*/


        const search = instantsearch({
            indexName: this.indexName,
            searchClient: this.searchClient
        });

        this.config = search;

        search.addWidgets([
            instantsearch.widgets.searchBox({
                container: "#searchbox"
            }),
            instantsearch.widgets.clearRefinements({
                container: "#clear-refinements"
            }),
            instantsearch.widgets.refinementList({
                container: "#published-list",
                attribute: "published"
            }),
            instantsearch.widgets.refinementList({
                container: "#features-list",
                attribute: "feature"
            }),
            instantsearch.widgets.refinementList({
                container: "#modes-list",
                attribute: "modes"
            }),
            instantsearch.widgets.refinementList({
                container: "#country-list",
                attribute: "country"
            }),
            /*
            instantsearch.widgets.refinementList({
                container: "#genres-list",
                attribute: "genres"
            }),
            instantsearch.widgets.refinementList({
                container: "#players-list",
                attribute: "players"
            }),
            instantsearch.widgets.refinementList({
                container: "#platforms-list",
                attribute: "platforms"
            }),
            */
            instantsearch.widgets.configure({
                hitsPerPage: 12,
                snippetEllipsisText: "...",
                attributesToSnippet: ["description:50"],
                filters: "published=true"
            }),

            /*
            instantsearch.widgets.refinementList({
                container: "#misc-list",
                attribute: "misc"
            }),
            */
            instantsearch.widgets.hits({
                container: "#hits",
                templates: {
                    item: `
                  <div>
                    <div class="hit-name">
                      name: {{name}}
                    </div>
                    <img src="{{image}}" align="left" />
                    <div class="hit-description">
                      {{#helpers.snippet}}{ "attribute": "description" }{{/helpers.snippet}}
                    </div>
                    <div class="hit-info">country: {{countryCode}}</div>
                    <div class="hit-info">features: {{feature}}</div>
                  </div>
                `
                }
            }),
            instantsearch.widgets.pagination({
                container: "#pagination"
            })
        ]);

        search.start();
    }

}
