import { Component, Injector, OnInit } from '@angular/core';
import { BasePage } from 'aigens-framework';
import { ConfirmDialogComponent } from '../../dialogs/confirm-dialog/confirm-dialog.component';
@Component({
    selector: 'app-aux2-page',
    templateUrl: './aux2-page.component.html',
    styleUrls: ['./aux2-page.component.scss']
})
export class Aux2PageComponent extends BasePage implements OnInit {

    
    constructor(injector: Injector) { 
        super(injector);
    }

    ngOnInit(): void {
       
        var routeData = this.route.snapshot.data;

        console.log("routeData", routeData);
    }

    getState(){
        return window.history.state;
    }

    goDialog(){
        var url = "test/dialog/aux1";

        this.dialogService.openAux(url);
    }

    goOrder(){
        console.log("go order");
        
        var path = "/order/store/500/mode/takeaway";

        //window.history.go(-2);

        //console.log("history state", window.history.state);

        this.routingService.navigate(path);

    }


    goPopup(){

        this.dialogService.openComponent(ConfirmDialogComponent, {}, {}, (result)=>{
            
            this.dialogService.showToast(JSON.stringify(result));

        });;

    }

    done(){

        this.dialogService.submitAuxDialog({"HELLO": "WORLD2"});

    }

}
