import { DataService } from '@aigens/core/src/app/framework';
import { BaseService } from 'aigens-framework';
import { Injectable, Injector } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class FavoriteItemService extends BaseService {

    private bookmarkUrl = '/api/v1/store/bookmark.json';
    constructor(injector: Injector, public dataService: DataService) { super(injector) }
    favoriteItems = []

    setFavoriteItem(favoriteI) {
        if (!this.favoriteItems.includes(favoriteI)){
            this.favoriteItems.push(favoriteI);
        }
    }

    removeFavoriteItem(favoriteI) {
        this.favoriteItems = this.favoriteItems.filter((x) => x !== favoriteI);
    }

    getBookmarks() {
        let params = {}
        params['brandId'] = this.context.brandId;
        params['groupId'] = this.context.groupId;
        params['type'] = 'Item';
        params['locale'] = 'en';
        this.dataService.get(this.bookmarkUrl, params).forEach((data) => {
            data?.forEach(data => {
                this.setFavoriteItem(data.contentId);
            });
        }).catch((err) => {
            console.log(err);
        });
    }

    setBookmarkItem(itemId: string) {
        let params = {}
        params['brandId'] = this.context.brandId;
        params['groupId'] = this.context.groupId;
        params['type'] = 'Item';
        params['locale'] = 'en';
        params['contentId'] = itemId;
        return this.dataService.postWithParams(this.bookmarkUrl, params).forEach((data) => {
            this.setFavoriteItem(data.contentId);
        });

    }

    deleteBookmarkItem(itemId: string) {
        let params = {}
        params['brandId'] = this.context.brandId;
        params['groupId'] = this.context.groupId;
        params['type'] = 'Item';
        params['locale'] = 'en';
        params['contentId'] = itemId;
        params['favorite'] = false;

        return this.dataService.postWithParams(this.bookmarkUrl, params).forEach((data) => {
            this.removeFavoriteItem(data.contentId);
        });
    }
}
