import { Injectable, Injector } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class ThemeTokenService {

    tokens: any[];

    constructor(injector: Injector) {


    }


    setTokens(tokens){

        this.tokens = tokens;

        var cssmap = this.tokens2CssMap(tokens);

        this.setCssVars(cssmap);
    }

    tokens2CssMap(tokens): any{
        var cssmap = {};

        for (let token of tokens) {

            var key = token.name;
            var value = token.value;

            cssmap[key] = value;
        }

        return cssmap;
    }

    setCssVars(cssmap){

        let root = document.documentElement;

        for (var key in cssmap) {
            var cssName = "--" + key;
            var value = cssmap[key];
            root.style.setProperty(cssName, value + "");
        }

    }

    rawStyles = {};

    addCss(clsName, cssStr){

        var style = this.rawStyles[clsName];

        if(!style){
            style = document.createElement('style');
            document.head.append(style);
            console.log("style ref", style);
        }
        //const style = document.createElement('style');
        style.textContent = cssStr;

    }


    listenTestCss(){

        console.log("listening test css!");

        window.addEventListener('message', e => {

            var data = e.data;

            console.log("child listen event", e, data);

            if(!data) return;

            if(data.cssmap){
                this.setCssVars(data.cssmap);
            }

        },false);

    }


}
