    newlogicAboutBenefitMax(discountBenefitMax, discountApplicable, unlockBenefitMax?): number {
        let max_ = discountBenefitMax * discountApplicable;
        return unlockBenefitMax ? Math.min(max_, unlockBenefitMax) : max_;
    }


