    /**
     * @description: checkOrderIsHasCurrentPromoCodeDiscount
     * @param {*}
     * @return {*}
     */
    checkOrderIsHasCurrentPromoCodeDiscount(): boolean {
        let { order } = this.orderManager;
        let appliedDiscounts: Discount[] = order?.discounts || [];

        return appliedDiscounts.some((appliedDiscount: Discount) => {
            return appliedDiscount?.conditions?.some((condition: Condition) => condition?.type === 'code' && condition?.promoCodeCheck(order.promoCode, condition?.code));
        });
    }

