    /**
     * @description: get discounts from order.discounts(applied) by benefit type
     * @param {*} type
     * @return {*}
     */
    getOrderDiscountsByBenefitType(type: 'unlock' | 'percent' | 'gift' | 'amount'): Discount[] {
        let { order } = this.orderManager;
        let appliedDiscounts: Discount[] = order?.discounts || [];
        return appliedDiscounts?.filter((appliedDiscount: Discount) => {
            return appliedDiscount?.benefits?.some((benefit: Benefit) => benefit?.type === type);
        });
    }

    /**
     * @description: get discounts from store.menu.discounts by benefit type
     * @param {*} type
     * @return {*}
     */
    filterDiscountsByBenefitType(type: 'unlock' | 'percent' | 'gift' | 'amount' | 'unlockItem', discounts: Discount[]): Discount[] {
        const disc: Discount[] = discounts || [];
        return disc?.filter((d: Discount) => {
            return d?.benefits?.some((benefit: Benefit) => benefit?.type === type);
        });
    }

