    /**
     * Check items availability on user's cart
     *
     */
    checkAvailabilityOrderItemOnCart(varTimestamp: number, varMode: string = null, varStore: Store = null, varOrder: Order = null): { validOrder: Order, invalidOrderItems: OrderItem[] } {
        const validOrder: Order = varOrder ? JSON.parse(JSON.stringify(varOrder)) : JSON.parse(JSON.stringify(this.orderManager.order));

        const invalidOrderItems: OrderItem[] = [];

        let error: string = null;

        if (this.orderManager.multi) { // multi OM
            validOrder.subs.forEach(subOrder => {
                const om = this.orderManager.findOm(subOrder.storeId);

                if (om) {
                    const store = om.store;
                    const menu = om.store.menu;
                    const mode = varMode ? varMode : om.mode;

                    const timestamp = this.convertNowDateToStoreTimeZone(store.timezone, varTimestamp).getTime();

                    // currently only process if om has menu
                    if (menu) {
                        const orderItems = JSON.parse(JSON.stringify(subOrder.orderitems));
                        subOrder.orderitems = [];
                        for (const oi of orderItems) {
                            const valid = this.isOrderItemValid(store, menu, mode, oi, timestamp);

                            if (!valid) {
                                invalidOrderItems.push(oi);
                            } else {
                                subOrder.orderitems.push(oi);
                            }
                        }
                    }
                }
            });
        } else { // single OM
            validOrder.orderitems = [];

            const store = varStore ? varStore : this.orderManager.store;
            const menu = varStore ? varStore.menu : this.orderManager.store.menu;
            const mode = varMode ? varMode : this.orderManager.mode;

            const orderItems = varOrder ? JSON.parse(JSON.stringify(varOrder.orderitems)) : this.orderManager.order?.orderitems;

            const timestamp = this.convertNowDateToStoreTimeZone(store.timezone, varTimestamp).getTime();
            // const timestamp = _timestamp;


            for (const oi of orderItems) {
                const valid = this.isOrderItemValid(store, menu, mode, oi, timestamp);
                if (!valid) {
                    error = valid[0]?.error;
                    invalidOrderItems.push(oi);
                } else {
                    validOrder.orderitems.push(oi);
                }
            }
        }

        return { validOrder, invalidOrderItems };
    }

    /**
     * Check whether order item is valid or not
     */
    public isOrderItemValid(store, menu, mode, oi, timestamp) {
        let valid: boolean = true;

        const realCat = MenuManager.getCategory(menu, oi.categoryId);

        if (realCat) {
            let checkPublish = true;
            let timestampForCheck = timestamp;

            // if category has type such as recommendation / gift / unlocks
            if (realCat.type) {
                checkPublish = false;
                timestampForCheck = this.menuManager.getTime(store);
            }

            // console.log('==== realCat', realCat, checkPublish, timestampForCheck);


            // need to check category availability also
            const availCat = this.menuManager.isAvailableCategory(store, Data.toData(Category,realCat), mode == 'takeaway',
                checkPublish, timestampForCheck, mode);

            if (!availCat) {
                valid = false;
            }

            for (const ig of oi.groups) {
                const realIg = MenuManager.getItemGroup(menu, ig.id);
                if (!realIg) {
                    valid = false;
                    continue;
                }
                for (const item of ig.items) {
                    const realItem = MenuManager.getItem(menu, realIg, item.id);

                    if (!realItem) {
                        valid = false;
                        continue;
                    }

                    const itemAvailable = this.menuManager.isAvailableItem(store, realCat, realIg, realItem,
                        mode == 'takeaway', timestampForCheck, mode);
                    if (!itemAvailable) {
                        valid = false;
                    }
                }
            }
        } else {
            valid = false;
        }
        return valid;
    }

    /**
     * Check items availability on user's cart
     *
     * @param timestamp
     * @param mode
     */
    checkAvailabilityOrderItemOnCartBeforeChangeMode(timestamp: number, mode: string): { validOrder: Order, invalidOrderItems: OrderItem[] } {
        const validOrder: Order = JSON.parse(JSON.stringify(this.orderManager.order));
        validOrder.orderitems = [];
        let orderItems;
        if (this.orderManager.order.subs) {
            const length = this.orderManager.order.subs.length - 1;
            orderItems = this.orderManager.order.subs[length].orderitems;
        } else {
            orderItems = this.orderManager.order.orderitems;
        }

        const menu = this.orderManager.store.menu;

        const invalidOrderItems: OrderItem[] = [];
        orderItems?.forEach(oi => {
            let valid = true;
            const realCat = MenuManager.getCategory(menu, oi.categoryId);

            for (const ig of oi.groups) {
                const realIg = MenuManager.getItemGroup(menu, ig.id);
                for (const item of ig.items) {
                    const realItem = MenuManager.getItem(menu, realIg, item.id);
                    const itemAvailable = this.menuManager.isAvailableItem(this.orderManager.store, realCat, realIg, realItem,
                        this.orderContext.mode == 'takeaway', timestamp, mode);
                    if (!itemAvailable) {
                        valid = false;
                    }
                }
            }

            if (!valid) {
                invalidOrderItems.push(oi);
            } else {
                validOrder.orderitems.push(oi);
            }
        });

        return { validOrder, invalidOrderItems };
    }

