    /**
     * @description: // ois (Can the orderItems be ordered)  tips: reOrder-flow can reuse this function
     * @param {OrderItem} orderItems
     * @param {*} mode
     * @return {*}
     */
    isCanOrder(orderItems: OrderItem[]): boolean {
        let allCanOrderBooleanArr = []; // save the oi canOrderByCurrentOi
        if (orderItems?.length > 0) {
            for (let i = 0; i < orderItems.length; i++) {
                const oi = orderItems[i];
                let canOrderByCurrentOi = this.isCanOrderByOI(oi);
                allCanOrderBooleanArr.push(canOrderByCurrentOi);
            }
        }
        // console.log('allCanOrderBooleanArr', allCanOrderBooleanArr);
        return allCanOrderBooleanArr.every(bool => bool);
    }

    /**
     * @description: // oi (Can the oi be ordered)
     * @param {OrderItem} oi
     * @return {*}
     */
    isCanOrderByOI(oi: OrderItem): boolean {

        // check oi.cat  cat 
        let oiCatId = oi.categoryId;
        let oiCat: Category = this.orderContext.availableCategories.find(cat => cat?.id === oiCatId);
        // check oi.items  item 
        let allItemCanOrderBooleanArr = oiCat && oi && oi.items && oi.items.map(item => this.isCanOrderByItem(item, this.orderContext.mode));
        console.log('allItemCanOrderBooleanArr', allItemCanOrderBooleanArr);
        return allItemCanOrderBooleanArr?.every(bool => bool);
    }

    /**
     * @description: // item (Can the item be ordered?)
     * @param {Item} item
     * @param {*} mode
     * @return {*}
     */
    isCanOrderByItem(item: Item, mode = 'takeaway'): boolean {
        const origin = this?.orderManager?.store?.menu?.['imap'][item?.id];
        if (origin) {
            item = origin;
        }
        let itemPeriods = item && item.periods || [];
        // console.log('isCanOrderByItem', item.name + ' periodsCode:', itemPeriods)
        let timeOk = false;

        let userSelectTime = this.menuService?.getUserSelectTime_number();
        console.log('userSelectTime', userSelectTime);
        // check item  userSelectTime 
        if (!this.menuManager.isAvailabeByPeriods(this.orderContext.store, itemPeriods, userSelectTime)) {
            console.log('periods filter', item.name);
            timeOk = false;
        } else {
            timeOk = true;
        }

        // check oi item mode  store mode
        let modeOk = true;
        if (item?.modes && item?.modes.length > 0) {
            modeOk = item.modes.indexOf(mode) > -1;
        }

        // check oi  item
        let invOk = !this.inventoryManager.isSoldout(item);

        return timeOk && modeOk && invOk; // 
    }


    /**
     * @description: jugde by whether item and item.variations arr all soldout
     * @param {Item} item
     * @return {*}
     */
    isVariationsAllSoldout(item: Item): boolean {
        if (!item) {
            return false;
        }
        if (!item.variations || item.variations.length === 0) {
            return this.isSoldout(item);
        }
        let items = [item, ...item?.variations];
        return items?.every(item => this.isSoldout(item));
    }


