import { Injectable, Injector } from '@angular/core';
import { PlatformService } from './platform.service';

@Injectable({
    providedIn: 'root'
})
export class PwaService {

    supported: boolean = false;
    manifest: any;

    constructor(injector: Injector, private platformService: PlatformService) {

    }

    haveServiceWorker(){

        if(navigator.serviceWorker){
            return true;
        }

        return false;
    }

    setWebManifest() {

        if(this.manifest || !this.haveServiceWorker()) return;

        //Aliapp never use service worker
        if(this.platformService.isAliApp()){
            return;
        }

        var manifest = window['aigens']?.manifest;
        if (!manifest || !manifest.scope) return;

        //copy it
        manifest = Object.assign({}, manifest);

        //console.log("setting manifest", manifest);


        var loc = window.location;
        var host = loc.protocol + "//" + loc.host;

        

        manifest.scope = host + manifest.scope;
        manifest.start_url = host + manifest.start_url;

        console.log("setting patched manifest", manifest);

        const stringManifest = JSON.stringify(manifest);
        const blob = new Blob([stringManifest], { type: 'application/json' });
        const manifestURL = URL.createObjectURL(blob);

        var link = document.getElementById('dy-manifest');
        if (!link) return;

        console.log("manifest url", manifestURL);

        link.setAttribute('href', manifestURL);

        this.manifest = manifest;

        this.listen();

    }

    deferredPrompt: any;

    listen() {

        console.log("listening add to home");

        window.addEventListener('beforeinstallprompt', (e) => {

            this.supported = true;

            console.log("beforeinstallprompt", e);

            // Prevent Chrome 67 and earlier from automatically showing the prompt
            e.preventDefault();
            // Stash the event so it can be triggered later.
            this.deferredPrompt = e;
            // Update UI to notify the user they can add to home screen
            //addBtn.style.display = 'block';

            

        });
    }

    promptHome() {

        // hide our user interface that shows our A2HS button
        //addBtn.style.display = 'none';
        // Show the prompt
        this.deferredPrompt.prompt();
        // Wait for the user to respond to the prompt
        this.deferredPrompt.userChoice.then((choiceResult) => {
            if (choiceResult.outcome === 'accepted') {
                console.log('User accepted the A2HS prompt');
            } else {
                console.log('User dismissed the A2HS prompt');
            }
            this.deferredPrompt = null;
        });


    }

}
