import { Component, OnInit } from '@angular/core';
import FingerprintJS from '@fingerprintjs/fingerprintjs'
import { DeviceUUID } from 'device-uuid';
@Component({
    selector: 'app-fingerprint-page',
    templateUrl: './fingerprint-page.component.html',
    styleUrls: ['./fingerprint-page.component.scss']
})
export class FingerprintPageComponent implements OnInit {

    visitorId: string = '';
    deviceUUID: string = '';

    constructor() { }

    ngOnInit(): void {
        const fpPromise = FingerprintJS.load({
            monitoring: false
        })
        ; (async () => {
            const fp = await fpPromise
            const result = await fp.get()
            this.visitorId = result.visitorId
        })();

        this.deviceUUID = new DeviceUUID().get();
    }

}
