import { PwaService } from '@aigens/core/src/app/pure/services/pwa.service';
import { Component, Injector, OnInit } from '@angular/core';
import { BasePage } from 'aigens-framework';

@Component({
    selector: 'app-pwa-page',
    templateUrl: './pwa-page.component.html',
    styleUrls: ['./pwa-page.component.scss']
})
export class PwaPageComponent extends BasePage implements OnInit {

    brand: any;
    brandId: any;
    storeId: any;
    inited: boolean = false;

    constructor(private injector: Injector, private pwaService: PwaService) { 
        super(injector);
    }

    ngOnInit(): void {


        this.autoContext();
        

    }

    init(){
        this.inited = true;
        this.brand = this.context.brand;
        this.pwaService.setWebManifest();
    }


    addHome(){
        this.pwaService.promptHome();
    }
}
