import { MemberContextManager } from '@aigens/core/src/app/framework/services/member-context-manager.service';
import { StorageService } from '@aigens/core/src/app/framework/services/storage.service';
import { SubscribeService } from '@aigens/core/src/app/pure/services/subscribe.service';
import { ChangeDetectorRef, Component, Injector, OnInit } from '@angular/core';
import { BasePage, LocationService } from 'aigens-framework';
import { ShareService } from '../../../../../../core/src/app/framework/services/share.service';
