@Component({
    selector: 'app-kiosk-page',
    templateUrl: './kiosk-page.component.html',
    styleUrls: ['./kiosk-page.component.scss']
  })
export class KioskPageComponent extends BasePage implements OnInit {

    navigator = navigator;

    localUrl: any;
    haveServiceWorker: boolean = false;
    aigens: any;

    response: any;

    channel: string;
    nativePlatform: string;

    native: boolean;
    appInfo: any;

    constructor(private injector: Injector, private storageService: StorageService, private memberContextManager: MemberContextManager, private sub: SubscribeService,
        private pwaService: PwaService, private locationService: LocationService, public bridgeService: BridgeService,
        private cd: ChangeDetectorRef, public nativePlatformService: NativePlatformService, private shareService: ShareService, private nativeContainerService: NativeContainerService


    ) {



        super(injector);

        this.localUrl = window.location.href + "";
        //this assume alipay lib is already loaded
        // this.my2 = window['my'];

        this.haveServiceWorker = this.pwaService.haveServiceWorker();

        this.aigens = window['aigens'];

        this.channel = this.nativePlatformService.getChannel();

        this.native = this.nativePlatformService.isNative();

        this.nativePlatform = this.nativePlatformService.getNativePlatform();


    }

    ngOnInit(): void {




    }




    loadTestContext() {


        var storeId = 500;

        this.contextManager.loadContext(this.context.fields, { storeId: storeId }, true, (context) => {



        });
    }


    requestGps() {

        //this.invokeMethod("getLocation", []);


        this.locationService.getGps((pos) => {
            console.log("pos", pos);

            this.response = {
                latitude: pos.latitude,
                longitude: pos.longitude
            };
        });


    }

    requestQr() {

    }

    requestEcho() {



    }

    echo(){

        console.log("calling echo");

        var msg = {
            "HELLO": "WORLD",
            "value": "HELLOWORLD"
        };

        this.nativeContainerService.echo(msg).then((result)=>{
            console.log("ECHO RESULT", result);
            this.response = result;
        });
    }

    goAppHome() {

        console.log("go app hoe");
        var url = "/app/home/brand/4806394001752064";
        this.router.navigate([url], { queryParams: {} });
    }

    statusJson;

    testNetwork() {

        var url = "https://aigensstoretest.appspot.com/api/v1/status.json";
        this.dataService.getRaw(url, {}).subscribe((data) => {

            this.response = data;

        });


    }

    getSystemInfo() {

    }

    trackAnalytics(){

        console.log("trackAnalytics");
        this.analyticsService.trackSelectMode("byod");

    }



    finishApp(){

        this.routingService.finish();
    }

    exitApp(){


        this.routingService.exit();
    }
}

