import { Address, Store } from '@aigens/core/src/app/data';
import { DataService } from '@aigens/core/src/app/framework';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CateringService {

    distributeMethod: 'takeaway' | 'delivery' = 'takeaway';

    pickupStore: Store;
    deliveryAddress: Address;

    constructor(public dataService: DataService) { }

    setDistributeMethod(method: 'takeaway' | 'delivery'): void {
        this.distributeMethod = method;
    }

    getDistributeMethod(): 'takeaway' | 'delivery' {
        return this.distributeMethod;
    }

    setPickupStore(store: Store): void {
        this.pickupStore = store;
    }

    setPickupStoreId(id: number): void {
        this.fetchStoreObjectByStoreId(id);
    }

    setDeliveryAddress(address: Address): void {
        this.deliveryAddress = address;
    }

    getPickupStoreId(): number {
        return this.pickupStore?.id;
    }

    getDeliveryAddressId(): string {
        return this.deliveryAddress?.id;
    }

    private fetchStoreObjectByStoreId(storeId: number): void {
        const url = `/api/v1/store/store/${storeId}.json`;

        this.dataService.get(url).subscribe((store) => {
            if(store) {
                this.setPickupStore(store);
            }
        });
    }

    resetCatering(): void {
        this.deliveryAddress = null;
        this.pickupStore = null;

        this.distributeMethod = 'takeaway';
    }
}
