import { Injectable, Injector } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class ShareService {


    constructor(injector: Injector) {

    }

    isAvailable(){
        return (navigator as any)?.canShare;
    }

    share(options: any): Promise<any> {
        return navigator.share(options);
    }

}
