import { Component, Input, OnInit, EventEmitter, Output } from '@angular/core';
import { AnimationItem } from 'lottie-web';

@Component({
    selector: 'app-animation',
    templateUrl: './animation.component.html',
    styleUrls: ['./animation.component.scss']
})
export class AnimationComponent implements OnInit {

    _options: any = {};
    _src: string = '';
    _animationData: any;

    loaded: boolean = false;

    @Input()
    set src(src: string) {
        this._options = { ...this._options, path: src };
        this._src = src;
    }

    @Input()
    set animationData(data: any) {
        this._options = { ...this._options, animationData: data };
        this._animationData = data;
    }

    @Input()
    fallbackImgSrc: string = '';

    @Input()
    set options(options: any) {
        this._options = { ...options, path: this._src, animationData: this._animationData };
    }

    @Input()
    height: number = 500;
    @Input()
    width: number = 500;

    @Output()
    animationCreated: EventEmitter<AnimationItem> = new EventEmitter();

    constructor() { }

    ngOnInit(): void {
    }

    onError(error): void {
        console.log('ERROR', error);
    }

    onCreated(animation: AnimationItem): void {
        this.animationCreated.emit(animation);
    }

    onReady(): void {
        this.loaded = true;
    }

}
